<template>
    <div id="app" @contextmenu.prevent="openMenu" @click="closeMenu">
        <div :class="menu_class" :style="menu_style" ref="menu">
            <menu-head picture="assets/logo.png" label="WebDev - Ebalo"></menu-head>
            <hr>
            <div class="button-container">
                <item v-for="item of menuItems"
                      :separator="item.hasOwnProperty('separator') ? item.separator : false"
                      :label="item.hasOwnProperty('label') ? item.label : null"
                      @clicked="item.hasOwnProperty('callback') ? item.callback() : () => {}"
                      :key="item.id"></item>
            </div>
        </div>
    </div>
</template>

<script>
import MenuHead from "@/components/MenuHead";
import Item from "@/components/Item";

export default {
    name: 'App',
    components: {Item, MenuHead},
    data: function() {
        return {
            isVisible: false,
            menuLeft: 0,
            menuTop: 0,
            menuItems: [
                { id: 0, label: "Back", callback: this.back },
                { id: 1, label: "Forward", callback: this.forward },
                { id: 2, label: "Reload", callback: this.reload },
                { id: 3, separator: true },
                { id: 4, label: "Close", callback: this.closeMenu },
            ]
        };
    },
    methods: {
        closeMenu: function() {
            this.isVisible = false;
        },
        openMenu: function(event) {
            this.isVisible = true;
            
            const padding = 10;
            let x = event.clientX,
                    y = event.clientY;
            
            // x overflow check
            if(x < padding) {
                x = padding;
            }
            
            let max_x = window.innerWidth - this.$refs.menu.offsetWidth - padding;
            if(x > max_x) {
                x = max_x;
            }
            
            // y overflow check
            if(y < padding) {
                y = padding;
            }
            
            let max_y = window.innerHeight - this.$refs.menu.offsetHeight - padding;
            if(y > max_y) {
                y = max_y;
            }
            
            this.menuLeft = x;
            this.menuTop = y;
        },
        
        back: function() {
            alert('back-clicked');
            window.history.back();
        },
        forward: function() {
            alert('forward-clicked');
            window.history.forward();
        },
        reload: function() {
            alert('reload-clicked');
            location.reload();
        }
    },
    computed: {
        menu_class: function() {
            return `menu ${this.isVisible ? "show" : ""}`;
        },
        menu_style: function() {
            return `left: ${this.menuLeft}px; top: ${this.menuTop}px`;
        }
    }
}
</script>
